import { createWebHistory, createRouter } from "vue-router";
import Product from "./components/ProductComp.vue";
import Service from "./components/serviceComp.vue";
import game from "./components/GameComp.vue";
import company from "./components/CompanyComp.vue";
const routes = [
  {
    path: "/",
    component: Product,
  },
  {
    path: "/service",
    component: Service,
  },
  {
    path: "/game",
    component: game,
  },
  {
    path: "/company",
    component: company,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});
export default router;
