<template lang="">
  <div>
    <h2 @click="toggleGame1 = !toggleGame1">끝말잇기 게임</h2>
    <div class="gameArea" v-if="toggleGame1">
      <h3>제시어 : {{ word }}</h3>
      <label for="">정답</label>
      <input @keyup.enter="wordGame" type="text" v-model="insertValue" />
      <button @click="wordGame">insert</button>
      <span>{{ result }}</span>
    </div>
    <h2 @click="toggleGame2 = !toggleGame2">구구단 게임</h2>
    <div class="gameArea" v-if="toggleGame2">
      <label for="">Q : {{ num1 }} X {{ num2 }}</label>
      <input @keyup.enter="wordGame1" type="text" v-model="insertValue1" />
      <button @click="wordGame1">insert</button>
      <span>{{ result1 }}</span>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      word: "고라니",
      insertValue: "",
      result: "",
      insertValue1: "",
      result1: "",
      num1: Math.floor(Math.random() * (9 - 2) + 2),
      num2: Math.floor(Math.random() * (9 - 1) + 1),
      toggleGame1: false,
      toggleGame2: false,
    };
  },
  methods: {
    wordGame() {
      // 끝말잇기 게임
      //입력값으 첫글자 추출
      var answer = this.insertValue.slice(0, 1);
      var lastWord = this.word.slice(this.word.length - 1);
      if (lastWord == answer) {
        this.word = this.insertValue;
        this.insertValue = "";
        this.result = "정답입니다.";
      } else {
        this.insertValue = "";
        this.result = "오답입니다.";
      }
    },
    wordGame1() {
      if (this.num1 * this.num2 == this.insertValue1) {
        this.result1 = "정답입니다.";
        this.insertValue1 = "";
        this.num1 = Math.floor(Math.random() * (9 - 2) + 2);
        this.num2 = Math.floor(Math.random() * (9 - 1) + 1);
      } else {
        this.result1 = "오답입니다.";
        this.insertValue1 = "";
      }
    },
  },
};
</script>
<style scoped>
.gameArea {
  margin-bottom: 20px;
}
.gameArea > * {
  margin-right: 10px;
}
</style>
