<template lang="">
  <div id="container">
    <h2>product list</h2>
    <div class="in_wrap">
      <div class="contents" v-for="(c, i) in products" :key="i">
        <h3 @click="modalOpen(i)">{{ c.name }}</h3>
        <img @click="modalOpen(i)" :src="c.image" :alt="c.id" />
        <Modal
          :modal="modal"
          :products="products"
          :crt="crt"
          @closeModal="modalClose"
        ></Modal>
      </div>
    </div>
    <button class="more" @click="more">더보기</button>
  </div>
</template>
<script>
import axios from "axios";
import products from "../assets/products.json";
import Modal from "./ModalComp.vue";

export default {
  data() {
    return {
      products: products,
      modal: false,
      crt: 0,
      moreList: 0,
    };
  },
  components: {
    Modal,
  },
  methods: {
    modalOpen(i) {
      this.crt = i;
      this.modal = true;
    },
    modalClose() {
      this.modal = false;
    },
    more() {
      axios
        .get(`https://ldw0755.github.io/data/moreProducts.json`)
        .then((ret) => {
          console.log(ret.data.length);
          for (let i = 0; i < ret.data.length; i++) {
            const element = ret.data[i];
            this.products.push(element);
          }
        })
        .catch((e) => {
          console.trace(e);
        });
    },
  },
};
</script>
<style scoped>
#container {
  margin-bottom: 10rem;
}
.in_wrap {
  justify-content: space-around;
  display: flex;
  flex-wrap: wrap;
  cursor: pointer;
}
img {
  /* width: auto; */
  width: 200px;
  height: auto;
}
h3 {
  color: gray;
  font-size: 1.6rem;
  margin: 3rem 0 1rem;
  position: relative;
}
h3:after {
  display: block;
  content: "";
  width: 10%;
  height: 0.5rem;
  background-color: gainsboro;
  margin: 1rem auto;
}
.more {
  border: none;
  color: rgb(77, 77, 77);
  border-radius: 2rem;
  padding: 0 1rem;
  display: inline-block;
  margin-top: 3rem;
  line-height: 3rem;
  cursor: pointer;
}
</style>
